// 
// url

// axios
import request from '@/utils/request.js'

// token
// import { getToken } from '@/utils/token'

// (return{})
export const getAllChannelsAPI = () =>
  request({
    url: '/v1_0/channels',
    method: 'GET'
  })

// 
export const loginAPI = ({ mobile, code }) => {
  return request({
    url: '/v1_0/authorizations',
    method: 'POST',
    data: {
      mobile,
      code
    }
  })
}

// token
export const getNewTokenAPI = () => {
  return request({
    url: '/v1_0/authorizations',
    method: 'PUT',
    headers: {
      Authorization: `Bearer ${localStorage.getItem('refresh_token')}`
    }
  })
}

// ()
export const getUserChannelsAPI = () => request({
  url: '/v1_0/user/channels',
  method: 'GET'
  // token
  // headers: {
  //   Authorization: `Bearer ${getToken()}`
  // }
})

// 
export const userFollowedAPI = ({ userId }) => {
  return request({
    url: '/v1_0/user/followings',
    method: 'Post',
    data: {
      target: userId
    }
  })
}
// 
export const userUnFollowedAPI = ({ userId }) => {
  return request({
    url: `/v1_0/user/followings/${userId}`,
    method: 'DELETE'
  })
}

// 
export const articlesListAPI = ({ channel_id, timestamp }) => {
  return request({
    url: '/v1_0/articles',
    method: 'GET',
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // },
    params: {
      channel_id,
      timestamp
    }
  })
}

// 
export const dislikeArticle = ({ artId }) => {
  return request({
    url: '/v1_0/article/dislikes',
    method: 'POST',
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // },
    // requesttoken
    data: {
      target: artId
    }
  })
}

// 
export const articleReportsAPI = ({ artId, value }) => {
  return request({
    url: '/v1_0/article/reports',
    method: 'POST',
    data: {
      target: artId,
      type: value,
      remark: ',,'
    }
    // headers: {
    //   Authorization: `Bearer ${getToken()}`
    // }
  })
}

// API
export const articleDetailAPI = ({ art_id }) => {
  return request({
    url: `/v1_0/articles/${art_id}`
  })
}

// 
export const articleLikeAPI = ({ artId }) => {
  return request({
    url: '/v1_0/article/likings',
    method: 'POST',
    data: {
      target: artId
    }
  })
}
// 
export const articleDisLikeAPI = ({ artId }) => {
  return request({
    url: `/v1_0/article/likings/${artId}`,
    method: 'DELETE'
  })
}

// 
export const updateChannelAPI = ({ channels }) => {
  return request({
    url: '/v1_0/user/channels',
    method: 'PUT',
    data: {
      channels
    }
  })
}

// -
export const suggestListAPI = ({ kw }) => {
  return request({
    url: '/v1_0/suggestion',
    params: {
      q: kw
    }
  })
}

//  - 
export const searchResultListAPI = ({ kw, page = 1, per_page = 10 }) => {
  return request({
    url: '/v1_0/search',
    params: {
      q: kw,
      page,
      per_page
    }
  })
}

// 
export const commentListAPI = ({ artId, offset = null, limit = 10 }) => {
  return request({
    url: '/v1_0/comments',
    params: {
      type: 'a',
      source: artId,
      offset,
      limit
    }
  })
}

//  - 
export const commentLikingAPI = ({ comId }) => {
  return request({
    url: '/v1_0/comment/likings',
    method: 'POST',
    data: {
      target: comId
    }
  })
}
// -
export const commentDisLikingAPI = ({ comId }) => {
  return request({
    url: `/v1_0/comment/likings/${comId}`,
    method: 'DELETE'
  })
}

// 
export const sendCommentAPI = ({ artId, content, art_id = null }) => {
  // 
  const obj = {
    target: artId,
    content: content
  }
  if (art_id !== null) {
    obj.art_id = art_id
  }
  return request({
    url: '/v1_0/comments',
    method: 'POST',
    data: obj
  })
}

// ()
export const userInfoAPI = () => {
  return request({
    url: '/v1_0/user'
  })
}
// 
export const userProfileAPI = () => {
  return request({
    url: '/v1_0/user/profile'
  })
}

// 
export const updateUserPhotoAPI = (fd) => {
  return request({
    url: '/v1_0/user/photo',
    method: 'PATCH',
    data: fd // fd()
  })
}

//  - 
export const updateProfileAPI = ({ birthday, userName }) => {
  return request({
    url: '/v1_0/user/profile',
    method: 'PATCH',
    data: {
      birthday: birthday,
      name: userName
    }
  })
}
